import { drawing as draw } from '@progress/kendo-drawing';

import { ChartElement } from '../../core';

import { isFunction, setDefaultOptions } from '../../common';

class LineSegment extends ChartElement {
    constructor(linePoints, series, seriesIx) {
        super();

        this.linePoints = linePoints;
        this.series = series;
        this.seriesIx = seriesIx;
    }

    points(visualPoints) {
        const linePoints = this.linePoints.concat(visualPoints || []);
        const points = [];

        for (let i = 0, length = linePoints.length; i < length; i++) {
            if (linePoints[i].visible !== false) {
                points.push(linePoints[i]._childBox.toRect().center());
            }
        }

        return points;
    }

    createVisual() {
        const { options, series } = this;
        let { color, _defaults: defaults } = series;

        if (isFunction(color) && defaults) {
            color = defaults.color;
        }

        const line = draw.Path.fromPoints(this.points(), {
            stroke: {
                color: color,
                width: series.width,
                opacity: series.opacity,
                dashType: series.dashType
            },
            zIndex: series.zIndex
        });

        if (options.closed) {
            line.close();
        }

        this.visual = line;
    }

    aliasFor(e, coords) {
        return this.parent.getNearestPoint(coords.x, coords.y, this.seriesIx);
    }
}

setDefaultOptions(LineSegment, {
    closed: false
});

export default LineSegment;